<template>
  <div class="image">
      <van-button @click="getImage" type="info">相册</van-button>
      <img :src="this.src" width="100px" height="100px"/>
  </div>
</template>
<script>
import img from './img.vue';
export default {
  components: { img },
    data(){
        return{
            src:''
        }
    },
  mounted() {
      console.log(this.plus);   
  },
  methods:{
      getImage(){        
         this.plus.gallery.pick(function(e) {  
    	    	var name = e.substr(e.lastIndexOf('/') + 1);
    	       compressImage(e,name);
    	    }, function(e) {  
    	    }, {  
    	        filter: "image"  
    	    });
      }
  }
  
};
</script>

<style scoped>
    .image{
        width: 100%;
        height: 100%;
        position: absolute;
        bottom: 0;right: 0;left: 0;top:0;
    }
</style>


