<template>
    <div style="width:200px;height:200px">
        <img :src="src" alt="" srcset="" >
    </div>
</template>

<script>
export default {
    data(){
        return{
            src:''
        }
    },
    created(){
        this.src = this.$route.query.src
    }
}
</script>


